const getters = {
  name: state => state.permission.name,
  roles: state => state.permission.roles,
  token: state => state.permission.token,

  routers: state => state.permission.routers,
  subroutes: state => state.permission.subroutes,
  mainNavRoute: state => state.permission.mainNavRoute,
  subNavRoute: state => state.permission.subNavRoute
}
export default getters



