import Vue from 'vue'
import Router from 'vue-router'
/* layout */
import Layout from '../views/layout/Layout' // 这个layout应该就是直接是vue文件

const _import = require('./_import_' + process.env.NODE_ENV)

// console.log(_import);
// function (file) {
//   return __webpack_require__(1098)("./" + file + '.vue').default;
// }

// in development env not use Lazy Loading,because Lazy Loading too many pages will cause webpack hot update too slow.so only in production use Lazy Loading
Vue.use(Router)

/**
 * hidden : if `hidden:true` will not show in the navbar 属于非模板页面
 * redirect : if `redirect:noredirect` will no redirct in the levelbar
 * meta : { role: ['admin'] }  will control the page role
 **/
export const constantRouterMap = [{
  path: '/login',
  component: _import('login/index'),
  hidden: true
}, {
  path: '/404',
  component: _import('errorPage/404'),
  hidden: true
}, {
  path: '/401',
  component: _import('errorPage/401'),
  hidden: true
}]

export default new Router({
  scrollBehavior: () => ({
    y: 0
  }),
  routes: constantRouterMap
})

export const asyncRouterMap = [{
  path: '/infoQuery',
  component: Layout,
  redirect: '/infoQuery/index',
  name: '信息查询',
  children: [{
    path: 'index',
    component: _import('infoQuery/index'),
    name: '用户查询'
  }, {
    path: 'txtQuery',
    component: _import('infoQuery/txtQuery'),
    name: '短信查询'
  }, {
    path: 'themeQuery',
    component: _import('infoQuery/themeQuery'),
    name: '活动查询'
  }]
}, {
  path: '/taskCenter',
  component: Layout,
  redirect: '/taskCenter/index',
  name: '任务中心',
  children: [{
    path: 'index',
    component: _import('taskCenter/index'),
    name: '待回复任务'
  }, {
    path: 'outCall',
    component: _import('taskCenter/outCall'),
    name: '外呼任务'
  }, {
    path: 'taskList',
    component: _import('taskCenter/taskList'),
    name: '任务列表'
  }, {
    path: 'taskManage',
    component: _import('taskCenter/taskManage'),
    name: '任务管理'
  }]
}, {
  path: '*',
  redirect: '/404',
  hidden: true
}]



