import fetch from './fetch'

export function loginByUsername (username, password) {
  const data = {
    username,
    password
  }
  // console.log('login---8')
  return fetch({
    url: '/login/login',
    method: 'post',
    data
  })
}

export function logout () {
  return fetch({
    url: '/login/logout',
    method: 'post'
  })
}



