import {
  loginByUsername,
  logout
} from '@/api/login'

/* eslint-disable import/no-duplicates */
import router from '@/router'

/* eslint-disable import/no-duplicates */
import {
  asyncRouterMap,
  constantRouterMap
} from '@/router'

const permission = {
  state: {

    // 用户信息
    name: '',
    token: '',

    // 权限
    roles: [],
    routers: constantRouterMap, // 默认权限
    subroutes: [],

    // nav点击路由事件
    mainNavRoute: 0, // 但是这个应该动态生成 在非hidden的第一主项
    subNavRoute: 0 // 默认显示第0项

  },

  mutations: {
    SET_NAME: (state, name) => {
      state.name = name
    },
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_ROLES: (state, roles) => {
      state.roles = roles
    },
    SET_ROUTERS: (state, routers) => {
      // 这里的routers是由 路由中的对象asyncRouterMap过滤而来
      router.addRoutes(routers) // 动态添加可访问路由表
      state.routers = constantRouterMap.concat(routers)
      let routerIndex = 0
      for (; routerIndex < state.routers.length; routerIndex++) {
        if (!state.routers[routerIndex].hidden) {
          state.mainNavRoute = routerIndex // 对mainNavRoute动态加载
          break
        }
      }
      state.subroutes = state.routers[state.mainNavRoute].children
    },
    SET_NAVROUTE: (state, navRoute) => {
      state.mainNavRoute = navRoute.mainNavRoute
      state.subNavRoute = navRoute.subNavRoute
      state.subroutes = state.routers[state.mainNavRoute].children
    }
  },

  actions: {
    // 用户名登录
    LoginByUsername ({
      commit
    }, userInfo) {
      const username = userInfo.username.trim()
      // console.log('store--permission---58')
      return new Promise((resolve, reject) => {
        loginByUsername(username, userInfo.password).then(response => {
          const data = response.data

          const roles = data.roles
          commit('SET_NAME', data.name)
          commit('SET_TOKEN', data.token)
          commit('SET_ROLES', roles)

          let accessedRouters
          if (roles.indexOf('admin') >= 0) {
            accessedRouters = asyncRouterMap
          } else {
            accessedRouters = filterAsyncRouter(asyncRouterMap, roles)
          }
          commit('SET_ROUTERS', accessedRouters)

          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },

    // 登出
    LogOut ({
      commit,
      state
    }) {
      return new Promise((resolve, reject) => {
        logout(state.token).then(() => {
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },

    // 前端 登出
    FedLogOut ({
      commit
    }) {
      return new Promise(resolve => {
        resolve()
      })
    },

    SetNavRoute ({
      commit
    }, navRoute) {
      commit('SET_NAVROUTE', navRoute)
    }
  }
}

/**
 * 通过meta.role判断是否与当前用户权限匹配
 * @param roles
 * @param route
 */
function hasPermission (roles, route) {
  if (route.meta && route.meta.role) {
    return roles.some(role => {
      // console.log(role); 单值
      return route.meta.role.indexOf(role) >= 0
    })
  } else {
    return true
  }
}

/**
 * 递归过滤异步路由表，返回符合用户角色权限的路由表
 * @param asyncRouterMap
 * @param roles
 */
function filterAsyncRouter (asyncRouterMap, roles) {
  const accessedRouters = asyncRouterMap.filter(route => {
    if (hasPermission(roles, route)) {
      if (route.children && route.children.length) {
        route.children = filterAsyncRouter(route.children, roles)
      }
      return true
    }
    return false
  })
  return accessedRouters
}

export default permission



