<template>

    <div  class="navbar-container">

      <span class="main-web">小贷</span>

      <div class="nav-main">
        <el-menu :default-active="activeIndex" mode="horizontal" background-color="#0a122b" active-text-color="#ffffff">
          <template v-for="(router, routeIndex) in routers">
             <el-menu-item :index="router.name" v-if="!router.hidden&&router.children.length>0" @click="handleRoute(routeIndex)">{{router.name}}</el-menu-item>
          </template>
        </el-menu>
      </div>

      <div class="right-info">
        <div class="logout">安全退出</div>
        <b class="divide-line"></b>
        <span class="user-name">您好 {{name}}</span>
      </div>

    </div>

</template>


<script>

import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      activeIndex: ''
    }
  },
  computed: {
    ...mapGetters([
      'routers',
      'mainNavRoute',
      'name'
    ])
  },
  mounted () {
    this.activeIndex = this.routers[this.mainNavRoute].name
  },
  methods: {
    handleRoute (routeIndex) {
      let nextPath = this.routers[routeIndex].path + '/' + this.routers[routeIndex].children[0].path
      this.$store.dispatch('SetNavRoute', {
        'mainNavRoute': routeIndex,
        'subNavRoute': 0
      }).then(() => {
        this.$router.push({
          path: nextPath
        })
      })
    },

    logout () {
      this.$store.dispatch('LogOut').then(() => {
        location.reload() // 为了重新实例化vue-router对象 避免bug
      })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>

  @import "src/styles/mixin.scss";
  .navbar-container {

    position: fixed;
    top: 0;
    left: 0;
    z-index: 101;
    width: 100%;
    height: $navbarHeight;
    background: $navbarColor;

    color:#aaa;

    .main-web{
      position: absolute;
      height: $navbarHeight;
      line-height: $navbarHeight;
    }

    .nav-main{

      width: $mainWidth;
      height: $navbarHeight;
      line-height: $navbarHeight;

      padding-left: $sidebarWidth;
      margin: 0 auto;
      .nav-ul{
        width: 100%;
        display: flex;

        .nav-li{
          width: 80px;
          &.selected{
            color:#fff;
          }
        }
      }
    }

    .right-info{
      position:absolute;
      right: 20px;
      top: 0;
      line-height: $navbarHeight;
      .logout{
        float: right;
      }
      .divide-line{
        float: right;
        width: 0;
        font-size: 0;
        height: 21px;
        margin: 20px 6px 0 6px;
        border-right: #aaa 1px solid;
      }
      .user-name{
        float: right;
      }
    }

  }



</style>



