const userMap = {
  admin: {
    roles: ['admin'],
    name: 'Super Admin',
    token: 'admin'
  },
  editor: {
    roles: ['editor'],
    name: 'Normal Editor',
    token: 'editor'

  }
}

export default {
  loginByUsername: config => {
    const {
      username
    } = JSON.parse(config.body)
    return userMap[username]
  },
  logout: () => 'success'
}



