<template>

  <div class="subnavbar-main">

      <el-menu :default-active="activeIndex" mode="horizontal" class="subnav-el">
        <template v-for="(router, routeIndex) in subroutes">
           <el-menu-item :index="router.name" @click="handleRoute(routeIndex)">{{router.name}}</el-menu-item>
        </template>
      </el-menu>
 
  </div>



</template>


<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      activeIndex: ''
    }
  },
  computed: {
    ...mapGetters([
      'routers',
      'subroutes',
      'mainNavRoute',
      'subNavRoute'
    ])
  },
  mounted () {
    this.activeIndex = this.subroutes[this.subNavRoute].name
  },
  methods: {
    handleRoute (routeIndex) {
      let mainNavRoute = this.mainNavRoute
      let nextPath = this.routers[mainNavRoute].path + '/' + this.routers[mainNavRoute].children[routeIndex].path

      this.$store.dispatch('SetNavRoute', {
        'mainNavRoute': mainNavRoute,
        'subNavRoute': routeIndex
      }).then(() => {
        this.$router.push({
          path: nextPath
        })
      })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>

  @import "src/styles/mixin.scss";

  .subnavbar-main{
    background: #fff;
    width: 100%;
    height: $navbarHeight;
    border-bottom: solid 1px #e6e6e6;
    .subnav-el{
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
    }
  }
</style>



