<template>
	<div class="app-wrapper" >
		<navbar></navbar>

		<div class="main-container">
			<sidebar></sidebar>
			<app-main></app-main>
		</div>
	</div>
</template>

<script>
import { Navbar, Sidebar, AppMain } from 'views/layout' // 这个地方引用了layout文件夹中的index文件

export default {
  name: 'layout',
  components: {
    Navbar,
    Sidebar,
    AppMain
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>

  	@import "src/styles/mixin.scss";

	.app-wrapper {
		@include clearfix;
		position: relative;
    	height: 100vh;
		width: 100%;
		background:$appColor;

		.main-container {
		    display: flex;
		    display: -webkit-flex;

			width: $mainWidth;
    		height: 100vh;
    		margin: 0 auto;
			background: $mainColor;
			padding-top: $navbarHeight;
		}
	}
</style>



